.BusinessData {
  font-family: Roboto, "sans serif";
  display: grid;
  margin-top: 30px;
  grid-template-columns: 15% 80%;
}

.BusinessData h1 {
  color: #2e3239;
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 5%;
}

.BusinessData p {
  margin: 8px 0 0px 0;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: #24272c;
}

.Btn {
  margin-top: 5% !important;
  margin-left: 0 !important ;
  border-radius: 5px !important;
}
